/* Secction 4 */

:root {
  --width: 100vw;
  --full-width: 100vw;

  @media (min-width: 42em) {
    --width: 42rem;
  }
  --angle: -11deg;
  --magic-number: 0.09719;
  --skew-padding: calc(var(--width) * var(--magic-number));
  --clip-padding: calc(var(--full-width) * var(--magic-number));
}

.content {
  max-width: 100vw;
  max-width: var(--width);
  margin: 0 auto;
  padding: 1.5em;
  position: relative;
}

.clip-path {
  position: relative;
  margin-top: calc((calc(100vw * 0.09719) * -1) - 2px);
  margin-top: calc((var(--clip-padding) * -1) - 2px);
  background-image: linear-gradient(
      rgba(0, 0, 0, 0.05) 50%,
      0,
      transparent 100%
    ),
    linear-gradient(-135deg, #3a0ca3, #4895ef);
  background-size: 0.5em 0.5em, 100% 100%;
  padding: calc(
      (calc(100vw * 0.09719) * 2) - (calc(100vw * 0.09719) - calc(100vw * 0.09719))
    )
    0 4em;
  padding: calc(
      (var(--clip-padding) * 2) - (var(--clip-padding) - var(--skew-padding))
    )
    0 4em;
  clip-path: polygon(
    0% calc(calc(100vw * 0.09719) * 2),
    100% 0%,
    100% 100%,
    0% 100%
  );
  clip-path: polygon(
    0% calc(var(--clip-padding) * 2),
    100% 0%,
    100% 100%,
    0% 100%
  );
  -webkit-clip-path: polygon(
    0% calc(calc(100vw * 0.09719) * 2),
    100% 0%,
    100% 100%,
    0% 100%
  );
  -webkit-clip-path: polygon(
    0% calc(var(--clip-padding) * 2),
    100% 0%,
    100% 100%,
    0% 100%
  );
}

